import { BASE_URL } from '@/api'
export default {
	name: '',
	props: {
		isOffer: Boolean,
		demand_detail: Object,
		demandImg: Array,
	},
	data () {
		return {

		}
	},
	onLoad () {

	},
	computed: {
		BASE_URL () {
			return BASE_URL
		}
	},
	methods: {
		onPreviewEvent (index) {
			let url = [];
			this.demandImg.forEach((val, index) => {
				url.push(BASE_URL + val)
			})
			uni.previewImage({
				current: url[index],
				urls: url
			})
		}
	},
}
